}
.postwrap:last-of-type {
  padding-bottom: 50px;
}
.postwrap.alignwide {
  padding-left: 0;
  padding-right: 0;
}
.postwrap .layout {
  width: 100%;
  position: relative;
  display: flex;
  gap: clamp(18.75px, 2vw, 25px);
}
.postwrap .layout.reverse {
  flex-direction: row-reverse;
}
.postwrap .layout .left {
  display: flex;
  flex-direction: column;
  width: 35%;
}
.postwrap .layout .right {
  display: flex;
  flex-direction: column;
  width: 65%;
}

/* 404 */
