/* variables */
/* classes */
.fadein {
  animation: fadeIn 0.22s ease;
}

.fadeout {
  animation: fadeOut 0.22s ease;
}

.slidein {
  animation: slideIn 0.22s ease;
}

.slideout {
  animation: slideOut 0.22s ease;
  animation-fill-mode: forwards;
}

/* Animations */
@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes fadeOut {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@-webkit-keyframes fadeOut {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
@keyframes slideIn {
  0% {
    left: -101vw;
  }
  100% {
    left: 0;
  }
}
@-webkit-keyframes slideIn {
  0% {
    left: -101vw;
  }
  100% {
    left: 0;
  }
}
@keyframes slideOut {
  0% {
    left: 0;
  }
  100% {
    left: -101vw;
  }
}
@-webkit-keyframes slideOut {
  0% {
    left: 0;
  }
  100% {
    left: -101vw;
  }
}
