.logo-spinner {
  position: absolute;
  top: calc(50% - 87px);
  left: calc(50% - 87px);
  background-image: url(/theme/assets/icons/spinner-logo.svg);
  background-size: 50%;
  background-repeat: no-repeat;
  background-position: center center;
  z-index: 1;
}

.spinner {
  animation: rotate 1.4s linear infinite;
  -webkit-animation: rotate 1.4s linear infinite;
  -moz-animation: rotate 1.4s linear infinite;
  width: 174px;
  height: 174px;
  position: relative;
}

.spinner {
  animation: rotate 1.4s linear infinite;
  -webkit-animation: rotate 1.4s linear infinite;
  -moz-animation: rotate 1.4s linear infinite;
  width: 174px;
  height: 174px;
  position: relative;
}

.spinner-dot {
  width: 274px;
